import { isHotkey } from 'is-hotkey';
import { adjustSz } from '../actions';

// 比较event.which 而不是 event.key, 否则中文输入下不生效
const isSzUpHotKey = isHotkey('mod+shift+.');
const isSzDownHotKey = isHotkey('mod+shift+,');

export default function onKeyDown(event, controller, next) {
  if (isSzUpHotKey(event)) {
    event.preventDefault();
    return controller.run('onAction', adjustSz(true));
  }
  if (isSzDownHotKey(event)) {
    event.preventDefault();
    return controller.run('onAction', adjustSz(false));
  }
  return next();
}
